<template>
<el-main>
<el-row>
  <el-col :span="12">
    <el-card shadow='never'>
     <div slot='header' class='clearfix'>
       <HeaderTitle :titleName='tabTitle' class='float-header' />
        <ul style="float:right">
            <li
              v-for="(tab, index) in tabs"
              @click="toggle(index, tab.state)"
              :class="{ active: activeTab == index }"
              :key="index"
            >
              {{ tab.name }}
            </li>
          </ul>
     </div>
    <div ref='statusBarChart'  class='property-chart' v-loading='propertyLoading'></div>
    <!--div ref='statusPieChart' v-show='activeTab===1' class='property-chart' v-loading='propertyLoading'></div-->
    </el-card>
  </el-col>
  <el-col :span='12'>
    <el-card shadow='never'>
      <div slot='header' class='clearfix'>
        <HeaderTitle titleName='状态时间图' class='float-header' />
        <el-date-picker
          size='mini'
          v-model="date"
          :clearable="false"
          :picker-options="pickerOption"
          value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
      </div>
      <el-row class='clock-container' v-loading='clockLoading'>
        <el-col :span='6'>
          <el-card v-for="(item,index) in timeStackData.slice(0,2)"
            :key='index'
            shadow='never' style='text-align:left;'>
            <div slot='header'
            :style="{color:COLOR[index]}">
              {{item.name}}
            </div>
            <div>
              {{item.value}}
            </div>
          </el-card>
        </el-col>
        <el-col :span='12' id='pie-clock-container'>
          <div ref='clockPieChart' class='clock-chart'></div>
        </el-col>
        <el-col :span='6'>
          <el-card v-for="(item,index) in timeStackData.slice(2,4)"
            :key='index'
            shadow='never' style='text-align:right;'>
            <div slot='header'
             :style="{color:COLOR[index+2]}">
              {{item.name}}
            </div>
            <div>
              {{item.value}}
            </div>
          </el-card>
        </el-col>
      </el-row>
   </el-card>
  </el-col>
</el-row>
  <el-col :span='24'>
    <el-card shadow='never'>
      <div slot='header' class='clearfix'>
          <HeaderTitle titleName='今日每小时设备效率趋势图'/>
      </div>
      <div ref='efficiencyLine' class='line-chart' v-loading='efficiencyLoading'></div>
    </el-card>
  </el-col>

  <el-col :span='24' >
    <el-card shadow='never'>
      <div slot='header' class='clearfix'>
          <HeaderTitle titleName='今日每小时设备周期趋势图'/>
      </div>
      <div ref='cycleLine' class='line-chart' v-loading='cycleLoading'></div>
    </el-card>
  </el-col>
</el-main>
</template>
<script>
import js from '@/assets/js/report/EquipmentStatus/currentInfo.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/report/EquipmentStatus/currentInfo.less';
</style>
