import { showErrorNotify } from '@/utils/notify';
import { showWarningMessage } from '@/utils/message';

// import { CommonService } from '@/services/common.service';
// 引入table组件
import VXETable from '@/components/common/Table';
import {
  EQUIPMENT_STATUS_REPORT,
  STATUS_CODE,
  TITLE,
  BTN_TEXT
} from '@/utils/constantsFile';
import { GetStatusRange } from '@/api/statusEquipmentReport.js';

export default {
  components: {
    VXETable
  },
  data() {
    return {
      tableLoading: false,
      BTN_TEXT,
      allData: [],
      tableDatas: [],
      tableColumns: [
        {
          field: 'startTime',
          title: '开始时间',
          align: 'center',
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss']
        },
        {
          field: 'endTime',
          title: '结束时间',
          align: 'center',
          formatter: 'formatDate'
        },
        {
          field: 'time',
          title: '持续时间',
          align: 'center',
          formatter: 'timeStampSeconds'
        },
        {
          field: 'state',
          title: '状态',
          align: 'center',
          formatter: ({ cellValue }) => {
            return EQUIPMENT_STATUS_REPORT.LEGEND[cellValue - 1];
          }
        }
      ],
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: ['Sizes', 'PrevPage', 'Number', 'NextPage', 'Total'],
        perfect: true
      },
      dateTime: [],
      deviceState: null,
      statusList: [
        {
          label: '全部',
          value: null
        },
        {
          label: '运行',
          value: 1
        },
        {
          label: '空闲',
          value: 2
        },
        {
          label: '离线',
          value: 3
        },
        {
          label: '告警',
          value: 4
        }
      ]
    };
  },
  props: ['params'],
  methods: {
    async findList() {
      try {
        this.tableLoading = true;
        const data = await GetStatusRange(
          {
            startTime: this.dateTime[0],
            endTime: this.dateTime[1],
            deviceState: this.deviceState,
            current: 1,
            size: 5
          },
          {
            conditionJson1: [
              {
                column: 'deviceCode',
                ptype: 'string',
                type: 'eq',
                operator: 1,
                value: this.params.deviceCode
              }
            ]
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.allData = data.rows[0].deviceLiveReportVo.timeList
            .map(el => ({
              ...el,
              time: el.time * 60 * 1000
            }))
            .reverse();
          this.tablePage.total =
            data.rows[0].deviceLiveReportVo.timeList.length;
          this.onTablePageChange(this.tablePage.currentPage, this.tablePage.pageSize);
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoading = false;
      }
    },
    onTablePageChange(currentPage, pageSize) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      // this.findList();
      this.tableDatas = this.allData.slice(
        pageSize * (currentPage - 1),
        pageSize * currentPage
      );
    },
    exportTable() {
      this.$refs.historyTable.exportDatax(this.params.deviceName + '状态报表');
    }
  },
  async created() {
    // this.tablePage.total = this.params.timeList.length;
    this.dateTime = [this.params.startTime, this.params.endTime];
    await this.findList();
    this.onTablePageChange(1, 10);
  }
};
