import { showErrorNotify } from '@/utils/notify';
import { showWarningMessage } from '@/utils/message';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// import { CommonService } from '@/services/common.service';
// 引入table组件
import VXETable from '@/components/common/Table';

import { GetEfficiencyTrend, GetCycleTrend, GetStatus24Hour } from '@/api/statusEquipmentReport';
import {
  EQUIPMENT_DATA,
  BTN_TEXT,
  PAGE_TITLE,
  ENERGY,
  MAINTAINTASK_STATUS,
  EQUIPMENT_STATUS_REPORT,
  // STATUS_CODE,
  TITLE,
  MSG,

  STATUS_CODE
} from '@/utils/constantsFile';

export default {

  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable
  },

  data() {
    return {
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      COLOR: EQUIPMENT_STATUS_REPORT.COLOR,
      activeTab: 0,
      date: this.moment(new Date()).format('YYYY-MM-DD') + ' 00:00:00',
      pickerOption: {
        disabledDate: (current) => {
          return current.getTime() > new Date().getTime();
        }
      },
      tabs: [
        {
          type: 'current',
          name: '柱状图',
          state: 0,
          showFlag: true
        },
        {
          type: 'history',
          name: '比例图',
          state: 1,
          showFlag: true
        }
      ],
      propertyData: [],
      // 状态时间图数据
      timePieData: {},
      efficiencyData: {
        label: [

        ],
        data: [

        ]
      },
      cycleData: {
        data: [

        ],
        label: [

        ]
      },
      propertyLoading: false,
      clockLoading: false,
      efficiencyLoading: false,
      cycleLoading: false

    };
  },
  computed: {
    tabTitle: function() {
      return this.tabs[this.activeTab].name;
    },
    yieldKey() {
      return this.$store.state.pageInfo.pageConfigInfo.yieldKey;
    },
    // 柱状图和比例图数据
    /*
    propertyData: function() {
      if (this.timePieData.stackData) { return this.timePieData.stackData.map(el => Number((el / 60).toFixed(2))); } else { return []; };
    },
    */
    // 四种状态统计数据
    timeStackData: function() {
      const data = EQUIPMENT_STATUS_REPORT.LEGEND.map((el, index) => ({
        name: el,
        value: (this.timePieData.stackData && this.timePieData.stackData[index]) || 0
      }));
      return data.map((el) => ({
        ...el,
        value: `${Math.floor(el.value / 60)}小时${Math.round(el.value % 60)}分钟`
      }));
    }
  },
  props: [
    'deviceCode', 'rowPropertyData'
  ],
  watch: {
    activeTab: function() {
      this.drawProperty();
    },
    propertyData: function() {
      this.drawProperty();
    },
    timePieData: function() {
      this.drawStackPie();
    },
    efficiencyData: function() {
      this.drawLine(this.$refs.efficiencyLine, '效率（%）', '时间（小时）', this.efficiencyData);
    },
    cycleData: function() {
      this.drawLine(this.$refs.cycleLine, '周期（秒）', '时间（小时）', this.cycleData);
    },
    date: function() {
      this.getTimeData();
    }

  },
  methods: {
    toggle(index, state) {
      this.activeTab = state;
    },
    /**
     * @description: 状态和比例图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    /*
    async getPropertyData() {

      try {
        this.propertyLoading = true;
        const data = await GetStatus24Hour({
          deviceCode: this.deviceCode,
          time: `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`
        });

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.propertyData = [
            data.rows.runTimeSum,
            data.rows.idleTimeSum,
            data.rows.offlineTimeSum,
            data.rows.warnTimeSum]
            .map(el => Number((el / 60).toFixed(2)));
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.propertyLoading = false;
      }

      this.propertyData = this.rowPropertyData.map(el => Number((el / 60).toFixed(2)));
    },
    */
    /**
     * @description: 状态时钟图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getTimeData() {
      try {
        this.clockLoading = true;
        const data = await GetStatus24Hour({
          deviceCode: this.deviceCode,
          time: this.date
        });
        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.timePieData = {
            dayCount: data.rows.cumulativeProduction,
            stackData: [data.rows.runTimeSum, data.rows.idleTimeSum, data.rows.offlineTimeSum, data.rows.warnTimeSum],
            rows: data.rows.timeList.map(el => ({
              name: el.state,
              value: el.time
            }))
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.clockLoading = false;
      }
    },
    /**
     * @description: 获取效率趋势图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getEfficiencyData() {
      try {
        const endTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`;
        const startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;
        this.efficiencyLoading = true;
        const data = await GetEfficiencyTrend(
          {
            deviceCode: this.deviceCode,
            startTime,
            endTime
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.efficiencyData = {
            label: data.rows.map(el => el.time),
            data: data.rows.map(el => el.value)
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.efficiencyLoading = false;
      }
    },
    /**
     * @description: 获取周期趋势图数据
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    async getCycleData() {
      try {
        this.cycleLoading = true;
        const endTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`;
        const startTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 00:00:00`;

        const data = await GetCycleTrend(
          {
            deviceCode: this.deviceCode,
            startTime,
            endTime
          }
        );

        if (data.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.cycleData = {
            label: data.rows.map(el => el.time),
            data: data.rows.map(el => el.value)
          };
        } else {
          showWarningMessage(data.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.cycleLoading = false;
      }
    },
    /**
     * @description: 绘制折线图
     * @param { ref } elem
     * @param {*} yLabel
     * @param {*} xLabel
     * @param {*} dataSource {label:[],data:[]}
     * @return {*}
     * @author: zhxy
     */
    drawLine(elem, yLabel, xLabel, dataSource) {
      const lineChart = this.$echarts.init(elem);
      const option = {
        color: ['rgb(27, 137, 247)'],
        tooltip: {
          trigger: 'axis',
          formatter: function(param) {
            return `${xLabel.slice(0, 2)}:${param[0].name}<br/>${yLabel.slice(0, 2)}:${param[0].data}`;
          }
        },
        xAxis: {
          type: 'category',
          // boundaryGap: false,
          data: dataSource.label.map(el => el.slice(-8)),

          name: xLabel,
          axisTick: {
            alignWithLabel: true
          }
        },
        yAxis: {
          type: 'value',

          name: yLabel

        },
        series: [{
          type: 'line',
          data: dataSource.data,
          // showSymbol: false,
          symbol: 'circle',
          symbolSize: 0.01,
          label: {
            normal: {
              show: true,
              position: 'outside'
            }
          }
        }]
      };
      lineChart.setOption(option, true);
      window.addEventListener('resize', () => {
        lineChart.resize();
      });
    },
    /**
     * @description: 切换柱状图或比例图（饼图）
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawProperty() {
      if (this.activeTab === 0) {
        this.drawPropertyBar();
      } else {
        this.drawPropertyPie();
      }
    },
    /**
     * @description: 绘制状态柱状图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawPropertyBar() {
      const barChart = this.$echarts.init(this.$refs.statusBarChart);
      const option = {
        color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        barMaxWidth: '60%',
        grid: {
          left: '3%',
          right: '6%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          data: EQUIPMENT_STATUS_REPORT.LEGEND,
          axisTick: {
            alignWithLabel: true
          },

          name: '状态',
          nameGap: '5'
        }],
        yAxis: [{
          type: 'value',
          name: '时间/h',
          splitLine: {
            show: false
          }
        }],
        series: [
          {
            type: 'bar',
            label: {
              show: true,
              position: 'top',
              color: 'auto'
            },
            data: this.propertyData.map((el, index) => ({
              name: EQUIPMENT_STATUS_REPORT.LEGEND[index],
              value: el,
              itemStyle: {
                color: EQUIPMENT_STATUS_REPORT.COLOR[index]
              }
            }))
          }
        ]
      };
      barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        barChart.resize();
      });
    },
    /**
     * @description: 绘制状态比例图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawPropertyPie() {
      const pieChart = this.$echarts.init(this.$refs.statusBarChart);
      const option = {
        tooltip: {
          trigger: 'item'
        },

        series: [
          {
            type: 'pie',
            name: '',

            label: {
              show: true,
              position: 'center',
              formatter: () => {
                const totalTime = this.propertyData.reduce((a, b) => (a + b), 0).toFixed(2);
                return `{a|${totalTime}}\n{b|共计（小时）}`;
              },
              rich: {
                a: {
                  color: '#c74746',
                  fontSize: 36
                },
                b: {
                  color: '#c74746'
                }
              }
            },
            tooltip: {
              formatter: '{b}:{d}%'
            },
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data: this.propertyData.map((el, index) => ({
              name: EQUIPMENT_STATUS_REPORT.LEGEND[index],
              value: el,
              itemStyle: {
                color: EQUIPMENT_STATUS_REPORT.COLOR[index]
              }
            }))
          }
        ]
      };
      pieChart.setOption(option, true);
      window.addEventListener('resize', () => {
        pieChart.resize();
      });
    },
    /**
     * @description: 绘制时钟状态图
     * @param {*}
     * @return {*}
     * @author: zhxy
     */
    drawStackPie() {
      const pieChart = this.$echarts.init(this.$refs.clockPieChart);
      const option = {
        tooltip: {
          trigger: 'item'
        },

        series: [
          {
            type: 'pie',
            name: '',

            label: {
              show: false,
              position: 'center',
              formatter: () => {
                // return `产量（个）\n${this.timePieData.dayCount}`;
                return `{a|${this.yieldKey}（个）}\n{b|${this.timePieData.dayCount}}`;
              },
              rich: {
                a: {
                  color: 'black',
                  fontSize: 20
                },
                b: {
                  color: 'blue',
                  fontSize: 20
                }
              }

            },
            emphasis: {
              label: {
                // show: false
              }
            },
            radius: ['50%', '70%'],
            center: ['50%', '50%'],
            data: this.timePieData.rows.map((el) => ({
              name: EQUIPMENT_STATUS_REPORT.LEGEND[el.name - 1],
              value: el.value,
              itemStyle: {
                color: el.name === 0 ? '#fff' : EQUIPMENT_STATUS_REPORT.COLOR[el.name - 1]
              },
              tooltip: {
                formatter: (params) => {
                  return `${params.name}:${Math.floor(params.value / 60)}小时${Math.round(params.value % 60)}分钟`;
                }
              }
            }))
          }
        ]
      };

      pieChart.setOption(option, true);
      window.addEventListener('resize', () => {
        pieChart.resize();
      });
    },
    init() {
      /*
      await Promise.all([
        // this.getPropertyData(),
        this.getTimeData(),

        this.getEfficiencyData(),
        this.getCycleData()
      ]);
      */
      // if (this.timePieData.stackData) {

      // }
      this.getTimeData().then(() => {
        // 更新柱状图/比例图数据
        this.propertyData = this.timePieData?.stackData?.map(el => Number((el / 60).toFixed(2))) || [];
      });

      this.getEfficiencyData();
      this.getCycleData();
    }
  },
  mounted() {
    this.init();
  }
};
