<template>
<div class="history_table">
   <div class="search_con">
      <span class="label">时间范围:</span>
      <el-date-picker
         class="dateTime"
         v-model="dateTime"
         type="datetimerange"
         align="left"
         size="mini"
         unlink-panels
         value-format="yyyy-MM-dd HH:mm:ss"
         :range-separator="BTN_TEXT.TO"
         :start-placeholder="BTN_TEXT.STARTTIME"
         :end-placeholder="BTN_TEXT.ENDTIME"
         :default-time="['00:00:00', '23:59:59']"
         ></el-date-picker>
      <span class="label">状态:</span>
      <el-select v-model="deviceState"  size="mini" placeholder="请选择">
         <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
         </el-option>
      </el-select>
      <el-button type="primary" style="margin-left:10px;"  size="mini" @click="findList()" >查询</el-button>
      <el-button type="primary" class="exportBtn" size="mini" @click="exportTable">导出</el-button>
   </div>
   <VXETable
      ref="historyTable"
      tableName=""
      :height="'500px'"
      :tableLoading="tableLoading"
      :tableDatas="tableDatas"
      :tableColumns="tableColumns"
      :highlightCurrentRow="true"
      :tablePage="tablePage"
      @onTablePageChange="onTablePageChange"
  >
  </VXETable>
</div>
</template>
<script>
import js from '@/assets/js/report/EquipmentStatus/historyInfo.js';
export default js;
</script>
<style lang="less" scoped>
@import '../../../assets/less/report/EquipmentStatus/historyInfo.less';
.history_table {
   padding: 10px;
}
.search_con {
   position: relative;
   margin-bottom: 10px;

   .label {
      margin-right: 8px;
   }
   .dateTime {
      margin-right: 12px;
   }
   .exportBtn {
      position: absolute;
      right: 10px;
      top: 0;
   }
}
</style>
